<template>
  <div id="docMain">
    <div
      class="chat-box"
      :style="{'height':showChatBox?'500px':'0px','width':showChatBox?'300px':'0'}"
    >
      <!-- :style="{'height':showChatBox?'500px':'0px'}" -->
      <div class="chat-header">
        聊天室
      </div>
      <div class="chat-container">
        <span
          class="item-1"
