              v-if="item.identity !== identity"
            >
              {{item.data}}
            </span>
            <span
              class="item-right"
              v-else
            >
              {{item.data}}
            </span>
          </span>
        </span>
        <span class="item-2">
          <input v-model="sendData" />
          <div
            class="send-btn"
            @click="sendMsgHandler"
