                >
                </i>
              </Badge>
            </span>
            <span class="menu-item">
              <input
                type="text"
                placeholder="点击别处发送消息"
                @blur="sendMsgHandler"
              />
            </span>
            <span class="menu-item">

            </span>
          </div>
        </div>
        <div class="content">
          <div class="content-container">
            <!-- 侧边栏 -->
            <div class="sidebar">
              <div class="sidebar-box">
