<script>
import Index from "./../components/Index";
import Badge from "./../components/Badge";
export default {
  name: "docMain",
  components: {
    Index,
    Badge,
  },
  data() {
    return {
      docs: [],
      sidebar: [],
      sendData: "",
      sendDataArr: [],
      identity: this.$cookie.getCookie("identity"),
      showChatBox: false,
      msgCount: 0,
    };
  },
  created() {
    this.fetchTestData();
    this.fetchUpdated();
  },
  mounted() {},

  updated() {},
  methods: {
    sendMsgHandler() {
      //
      this.sendDataArr.push({
        identity: this.$cookie.getCookie("identity"),
        data: this.sendData,
      });
      // 推送消息
      this.$ws.send(
        JSON.stringify({
          type: 2,
          data: this.sendData,
          identity: this.$cookie.getCookie("identity"),
          status: 0,
        })
      );
    },

    docUpdated() {
      console.log("updated");
      this.$server.fetchUpdated().then((res) => {
        console.log(":=>", res);
        let resLen = res.length;
        for (let i = 0; i < resLen; i++) {
          let name = res[i].Name;
          console.log(name);
          this.docs.map((ele) => {
            if (ele.Name === name) {
              ele["Content"] = this.toDoc(res[i].Content);
            }
          });
        }
      });
    },

    fetchUpdated() {
      let _this = this;
      this.$ws.onmessage = function (e) {
        let node = _this.$refs.item01;
        node.scrollTop = node.scrollHeight;
        let msg = JSON.parse(e.data);
        switch (msg.Type) {
          case 2: // 表示聊天消息
            if (msg.Identity != _this.$cookie.getCookie("identity")) {
              _this.msgCount++;
              _this.sendDataArr.push({
                data: msg.Data,
                identity: msg.Identity,
              });
            }
            break;
          case 3: // 文档相关操作
            _this.docUpdated();
            break;
        }
      };
    },

    toDoc(contents, fn) {
      for (let i = 0; i < contents.length; i++) {
        let value = contents[i];
        if (!value["Url"]) {
          contents.splice(i, 1);
          continue;
        }
        value["Method"] = value["Method"].toUpperCase();
        value["Params"] = this.handleParams(value["Params"]);
        value["Result"] = "";
        if (!value["Header"]) {
          value["Header"] = "multipart/form-data";
        }
        fn && fn(value);
      }
      return contents;
    },

    fetchTestData() {
      this.$server.fetchDocs().then((res) => {
        let docs = res.data.Docs;
        if (docs !== null && Array.isArray(docs)) {
          docs.forEach((ele) => {
            let sidebarItem = [];
            ele.Content = this.toDoc(ele.Content, null);
            this.sidebar.push(sidebarItem);
          });
          this.docs = docs;
        }
        console.log(this.docs);
      });
    },

    handleParams(params) {
      let ret = [];
      if (Array.isArray(params)) {
        for (let i = 0; i < params.length; i++) {
          let p = params[i];
          ret.push({
            name: p[0] || "",
            type: p[1] || "",
            explain: p[2] || "",
            remark: p[3] || "",
            other: p[4] || "",
            value: "",
          });
        }
      }
      return ret;
    },
  },
};
</script>

