import Vue from 'vue'
import App from './App.vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import config from './../public/config.json';
import ServerApi from './api/index'
import Cookie from './api/cookie'
import { v4 as uuidv4 } from "uuid";
// 路由注册
import Routes from './route'

// 读取json文件
Vue.prototype.$doc_server = config;
Vue.use(VueAxios, axios);
Vue.use(ElementUI);
Vue.config.productionTip = false;
// 挂载服务api
Vue.prototype.$server = new ServerApi();
// 挂载Cookie
Vue.prototype.$cookie = new Cookie();

let ws = new WebSocket("ws://192.168.0.110:8888/updated");
Vue.prototype.$ws = ws;

if (!(new Cookie().getCookie('identity'))) {
  let identity = uuidv4();
  new Cookie().setCookie('identity', identity);
}

//初始化向服务器发送key(注册) 
ws.onopen = function () {
  ws.send(JSON.stringify({
    type: 1, // 注册
    data: '',
    identity: new Cookie().getCookie('identity'),
    status: 0,
  }))
}



new Vue({
  router: Routes,
  render: h => h(App),
}).$mount('#app')
