

export default class Cookie {
    setCookie(k, v) {
        document.cookie = `${k}=${v}`
    }

    getCookie(k) {
        let cookies = document.cookie.split("; ")
        let cookiesLen = cookies.length
        let returnVal = ""
        for (let i = 0; i < cookiesLen; i++) {
            let vals = cookies[i].split("=")
            if (vals[0] === k) {
                returnVal = vals[1]
                break
            }
        }
        return returnVal
    }
}